/* App.js uses this css file */
/* bootstrap link is in index.html in public folder of project */
#board{
  margin-top:2%;
  padding-left: 30%;
}

#buttons{
  margin-top:1%;
  padding-left:30%;
}

.sBtn{
  margin-right: 1%;
  width:20%;
  /* width: 159px; */
}

.rBtn{
  margin-left: 0%;
  margin-right: 1%;
  width:20%;
  /* width: 159px; */
}

.cBtn{
  width:20%;
  /* width: 159px; */
}

.square{ /*filled (standard) squares, i.e. blank squares in the original generated puzzles; values are fixed*/
  background-color: #fafafa;
  border: 1px solid #303030;
  height: 54.7px;
  width: 54.7px;
  font-size: 30px;
  font-weight: bold;
  text-align: center;
}

.emptySquare{ /*filled square but with some modifications to differentiate from filled squares*/
  background-color: #fafafa;
  border: 1px solid #303030;
  height: 54.2px;
  width: 54.2px;
  font-size: 30px;
  text-align: center;
}

.redSquare{
  background-color: #fccbc7;
  border: 1px solid #303030;
  height: 54.2px;
  width: 54.2px;
  font-size: 30px;
  text-align: center;
}

.greenSquare{
  background-color: #c2ffd2;
  border: 1px solid #303030;
  height: 54.7px;
  width: 54.7px;
  font-size: 30px;
  text-align: center;
  font-weight: bold;
}

#leftBorder{
  border-left: 6px solid black;
  height: 495px;
  position: absolute;
  left: 30%;
}

#topBorder{
  border-top: 6px solid black;
  width: 495px;
  position: absolute;
  top: 3.5%;
}

#bottomBorder{
  border-top: 6px solid black;
  width: 495px;
  position: absolute;
  top: 81.5%;
}

#rightBorder{
  border-left: 6px solid black;
  height: 495px;
  position:absolute;
  left:68.25%;
}

#verticalOne{
  border-left: 6px solid black;
  height: 495px;
  position: absolute;
  left: 42.5%;
}

#verticalTwo{
  border-left: 6px solid black;
  height: 495px;
  position: absolute;
  left: 55.5%;
}

#horizontalOne{
  border-top: 6px solid black;
  width: 495px;
  position: absolute;
  top: 29.5%;
}

#horizontalTwo{
  border-top: 6px solid black;
  width: 495px;
  position: absolute;
  top: 55.5%;
}

/*css for removing the js default scrollwheel for input type numbers*/
input[type="number"]::-webkit-outer-spin-button, 
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}



